import React from "react";
import { motion } from 'framer-motion';
import Header from "./HeaderComp";

export default function Blog() {
    return (
        <motion.div initial={{opacity: 0}} animate={{opacity: 1}} exit={{opacity: 0}}>
			<Header />
			<main className="About">
				<h2>Comming soon.</h2>
			</main>
		</motion.div>
    );
}